
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
components: {
ViewBox,
XHeader,
Tab,
TabItem,
Scroller,
Group
},
data () {
return {
index: 0,
spendList: []
}
},
computed: {
},
mounted () {
this.initView()
},
methods: {
initView () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/getSpendList.do'
let data = {
action: 'init_request',
path: path,
params: {
token: self.$store.state.userInfo.token
}
}
self.$store.dispatch('normalRequest', data).then(res => {
if (res.response === 'success') {
self.spendList = res.data.spendList
} else {
self.$vux.toast.text(res.data)
}
})
}
}
}



